<template lang="pug">
  .formulario-ocorrencia
    form(v-on:submit.prevent="registrarOcorrencia")
      .dados
        .tipo
          label Tipo de ocorrência
          select(v-model="ocorrencia.tipo")
            option
            option Assalto
            option 2
          small(v-if="this.mensagensErro.erroTipo") {{this.mensagensErro.erroTipo}}
        .data
          label Data
          input(type="text" placeholder="Ex.: 15/06/2015", v-model="ocorrencia.data")
          small(v-if="this.mensagensErro.erroData") {{this.mensagensErro.erroData}}
        .horario
          label Horário
          .campos
            input(type="number", v-model="ocorrencia.horas")
            span :
            input(type="number", v-model="ocorrencia.minutos")
          small(v-if="this.mensagensErro.erroHorario") {{this.mensagensErro.erroHorario}}

      .informacoes-adicionais
        titulo-internas Informações adicionais
        .assalto(v-if="ocorrencia.tipo == 'Assalto'")
          .prejuizo
            label Prejuízo estimado
            .valor
              span R$
              input(type="number")
            //- small Informe o valor
          .sexo
            label Sexo da vítima
            select
              option Selecione
              option Feminino
              option Masculino
            //- small Informe o sexo

        .arquivo
          label Envie um anexo
          input(type="file")

        .detalhes
          label Ainda tem mais detalhes?
          textarea(placeholder="Forneça mais detalhes sobre o ocorrido. (Opcional)", rows="10")

        .botao
          button(type="submit") Registrar ocorrência agora

</template>

<script>
import TituloInternas from 'BASICS/TituloInternas'

export default {
  components: {
    TituloInternas
  },
  data() {
    return {
      ocorrencia: {
        tipo: '',
        data: '',
        horas: '',
        minutos: ''
      },
      mensagensErro: {
        erroTipo: '',
        erroData: '',
        erroHorario: ''
      }
    }
  },
  methods: {
    validaFormulario() {
      let status = true

      if(this.ocorrencia.tipo === '') {
        this.mensagensErro.erroTipo = 'Informe o tipo da ocorrência'
        status = false
      } else {
        this.mensagensErro.erroTipo = ''
      }
      
      if(this.ocorrencia.data === '') {
        this.mensagensErro.erroData = 'Informe a data da ocorrência'
        status = false
      } else {
        this.mensagensErro.erroData = ''
      }
      
      if(this.ocorrencia.horas === '') {
        this.mensagensErro.erroHorario = 'Informe a hora da ocorrência'
        status = false
      } else if(this.ocorrencia.horas !== '' && this.ocorrencia.minutos === '' ) {
        this.mensagensErro.erroHorario = 'Informe os minutos da ocorrência'
        status = false
      } else {
        this.mensagensErro.erroHorario = ''
      }

      return status
    },
    resetForm() {

    },
    registrarOcorrencia() {
      if(this.validaFormulario()) {
        console.log(this.ocorrencia)
      }
    }
  }
}
</script>


<style lang="scss">
  .formulario-ocorrencia {
    margin: 20px 0;

    label {
      margin-bottom: 15px;
    }

    .dados {
      display: flex;

      .tipo {
        display: flex;
        flex-direction: column;
        width: 100%;
      }

      .data {
        display: flex;
        flex-direction: column;
        margin: 0 20px;
      }

      .horario {
        display: flex;
        flex-direction: column;

        .campos {
          display: flex;
          align-items: center;

          input {
            width: 50px;
          }

          span {
            margin: 0 5px;
          }
        }
      }
    }

    .informacoes-adicionais {
      margin-top: 20px;

      h2 {
        margin-bottom: 20px;
      }

      .assalto {
        display: flex;

        .prejuizo,
        .sexo {
          display: flex;
          flex-direction: column;
        }

        .prejuizo {
          margin-right: 20px;
          span {
            margin-right: 5px;
          }

          .valor {
            display: flex;
            align-items: center;
          }
        }
      }

      .arquivo ,
      .detalhes {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
      }
      

      .botao {
        display: flex;
        justify-content: flex-end;
        margin-top: 20px;

        button {
          color: #fff;
          background: $cor-verde-claro;
          padding: 10px 80px;
          border: none;
          border-radius: 5px;
          font-size: 16px;

          &:hover {
            cursor: pointer;
            opacity: 0.7;
          }
        }
      }
    }
  }
</style>




