<template lang="pug">
  section.painel-ocorrencias
    breadcrumb(:itens="breadcrumb")
    .container
      titulo-internas Minhas ocorrências
      .filtros
        .tipo
          controle-label(:valor="'Tipo da ocorrência'", for='tipoOcorrencia')
          controle-input(:tipo="'select'", :opcoes='opcoes', id='tipoOcorrencia', @inputValor='filtro.tipo = $event.value')
        .categoria
          controle-label(:valor="'Categoria do objeto'", for='categoriaObjeto')
          controle-input(:tipo="'select'", :placeholder="'Selecione aqui'", :opcoes='opcoes', id='categoriaObjeto', @inputValor='filtro.categoria = $event.value')
        .data
          controle-label(:valor="'Data da ocorrência'", for='dataOcorrencia')
          controle-input(:tipo="'input'", :placeholder="'Selecione aqui'", :opcoes='opcoes', id='dataOcorrencia', @inputValor='filtro.data = $event.value')
        .periodo
          controle-label(:valor="'Período da ocorrência'", for='periodoOcorrencia')
          controle-input(:tipo="'select'", :opcoes='opcoes', id='periodoOcorrencia', @inputValor='filtro.periodo = $event.value')
        .busca
          controle-label(:valor="'Buscar ocorrência'", for='buscaOcorrencia')
          controle-input(:tipo="'input'", :type="'date'", :placeholder="'Digite aqui'", :opcoes='opcoes', id='buscaOcorrencia', @inputValor='filtro.busca = $event.value')
        .botao
          button.azul(@click='showValue') Aplicar filtro desejado
      .resultados
        h3 Resultado da sua busca/filtro
        ocorrencia(:ocorrencias="ocorrencias")

</template>

<script>
import Breadcrumb from 'BASICS/Breadcrumb'
import ControleInput from 'BASICS/ControleInput'
import ControleLabel from 'BASICS/ControleLabel'
import Ocorrencia from 'BASICS/Ocorrencia'
import TituloInternas from 'BASICS/TituloInternas'

export default {
  components: {
    Breadcrumb,
    ControleInput,
    ControleLabel,
    Ocorrencia,
    TituloInternas
  },
  data() {
    return{
      breadcrumb: [
        { nome: 'A' },
        { nome: 'B' }
      ],
      opcoes: [
        'op1',
        'op2',
        'op3'
      ],
      ocorrencias: [
        { data: '12/06/2015', titulo: 'Desentendimento familiar', status: 'verde', endereco: 'Rua Romulado Galvão Lopes, 1033, Lagoa Nova', 
          horario: 'Manhã às 9h25' },
        { data: '12/06/2015', titulo: 'Desentendimento familiar', status: 'vermelho', endereco: 'Rua Romulado Galvão Lopes, 1033, Lagoa Nova', 
          horario: 'Manhã às 9h25' },
        { data: '12/06/2015', titulo: 'Desentendimento familiar', status: 'laranja', endereco: 'Rua Romulado Galvão Lopes, 1033, Lagoa Nova', 
          horario: 'Manhã às 9h25' }
      ],
      filtro: {
        tipo: '',
        categoria: '',
        data: '',
        periodo: '',
        busca: ''
      }
    }
  },
  methods: {
    showValue() {
      console.log(this.filtro)
    }
  }
}
</script>

<style lang="scss">

.painel-ocorrencias {
  .filtros {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;

    .tipo,
    .categoria,
    .data,
    .periodo,
    .busca,
    .botao {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 32%;
      margin-bottom: 20px;

      label {
        margin-bottom: 20px;
      }
    }

    .botao {
      button {
        height: 40px;
        font-size: 16px;
        border-radius: 5px;
      }
    }
  }

  .resultados {
    margin-top: 30px;

    h3 {
      color: $cor-azul-claro;
      font-size: 16px;
      font-weight: normal;
      margin-bottom: 30px;
    }
  }
}

</style>





