<template lang="pug">
  .mapa-registro-ocorrencia
    .input
      controle-input(:tipo="'input'", :type="'text'", :placeholder="'Buscar endereço'", id="searchTextField")
      i.fa.fa-search(aria-hidden="true")
    #map
</template>

<style lang="scss">
.mapa-registro-ocorrencia {
  position: relative;

  .input {
    position: absolute;
    right: 80px;
    top: 10px;
    background: white;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 3px;
    box-shadow: 5px 5px 5px -5px rgba(0,0,0,0.75);
    max-width: 350px;
    width: 100%;

    input {
      border: none;
      flex: 1;
    }

    i {
      color: $cor-azul-claro;
      font-weight: bold;
      margin-right: 10px;
    }
  }
}
</style>


<script>
import ControleInput from 'BASICS/ControleInput'

export default {
  components: {
    ControleInput
  },
  data() {
    return {
      map: '',
      lat: '',
      lng: '',
      marker: ''
    }
  },
  mounted() {

    let myOptions = {
      zoom: 13,
      center: {
        lat: -5.779696186875594,
        lng: -35.200555348421176
      }
    }

    this.map = new google.maps.Map(document.getElementById('map'), myOptions)
    this.marker = new google.maps.Marker({map: this.map, title: 'Localização'})

    let input = document.getElementById('searchTextField');
    let options = {
      componentRestrictions: {country: 'br'}
    };

    let autocomplete = new google.maps.places.Autocomplete(input, options);

    autocomplete.addListener('place_changed', (event) => {
      this.lat = autocomplete.getPlace().geometry.location.lat()
      this.lng = autocomplete.getPlace().geometry.location.lng()

      let latlng = new google.maps.LatLng(this.lat, this.lng);
      this.marker.setPosition(latlng)
      this.map.setCenter(this.marker.getPosition());
    })

    this.map.addListener('click', (event) => {
      this.lat = event.latLng.lat()
      this.lng = event.latLng.lng()

      let latlng = new google.maps.LatLng(this.lat, this.lng);
      this.marker.setPosition(latlng)
    })
  }
}
</script>




